@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap");
* {
  margin: 0;
  padding: 0;
  border: 0;
  line-height: 1.4em;
  vertical-align: baseline;
  text-decoration: none;
  outline: 0;
  box-sizing: border-box;
  background-color: transparent;
  font-family: "Roboto", sans-serif;
}

body {
  background-color: #FFFFFF;
}

.container {
  width: 1440px;
  margin: 0 auto;
  min-height: 100vh;
}

h1 {
  font-size: 42px;
  font-weight: 700;
  margin-bottom: 24px;
}

p {
  font-size: 16px;
  font-weight: 300;
  margin-bottom: 24px;
  line-height: 24px;
}

.btn {
  padding: 14px 38px;
  background-color: #E4C1AE;
  max-width: fit-content;
  border-radius: 32px;
  color: #FFFFFF;
  font-size: 16px;
  font-weight: 700;
  border: 2px solid #E4C1AE;
  cursor: pointer;
}
.btn:hover {
  background-color: #FFFFFF;
  color: #E4C1AE;
}
.btn:active {
  scale: 0.9;
}

section {
  display: flex;
  justify-content: space-between;
  padding: 96px 230px;
}
section .picture {
  width: 505px;
}
section .picture img {
  object-fit: cover;
  width: 100%;
}
section .content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 380px;
}

/* LIGHTBOX */
#popup {
  position: fixed;
  z-index: 1000;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: none;
  top: 50%;
  transform: translateY(-50%);
}
#popup.active {
  display: flex;
  justify-content: center;
  align-items: center;
}
#popup .popup-wrapper {
  position: relative;
  background-color: #FFFFFF;
  padding: 56px 95px;
  width: 920px;
}
#popup .popup-wrapper p {
  margin-bottom: 0;
}
#popup .popup-wrapper p .counter {
  font-weight: 600;
}
#popup .popup-wrapper p.reset {
  color: #ff1379;
  cursor: pointer;
  display: none;
}
#popup .popup-wrapper p.reset.active {
  display: block;
}
#popup .popup-wrapper .close-btn {
  position: absolute;
  top: 40px;
  right: 40px;
  padding: 6px;
  cursor: pointer;
}
#popup .popup-wrapper .close-btn img {
  width: 12px;
}
#popup .popup-wrapper .close-btn:hover {
  scale: 1.3;
}

@media (max-width: 1440px) {
  .container {
    width: 960px;
  }
  section {
    flex-direction: column;
    align-items: center;
    padding: 96px 96px;
  }
  section .content {
    width: 505px;
  }
  h1 {
    margin-top: 24px;
  }
}
@media (max-width: 960px) {
  .container {
    width: 360px;
  }
  section {
    width: 100%;
    padding: 24px;
  }
  section .picture {
    width: 100%;
  }
  section .content {
    width: 100%;
  }
}